.atuiMediasplayer_Audioplayer.optionFloating{
    position:fixed;
    z-index:11;
    bottom:20px;
    left:10%;
    right:10%;
    margin:0;
    border:none;
    box-shadow:var(--atuiKernel_ShadowMedium);
    background-color:hsl(var(--atuiKernel_Color-N90) / 80%);
    -webkit-backdrop-filter:var(--atuiKernel_Blur);
            backdrop-filter:var(--atuiKernel_Blur);
    display:none;
}

.atuiMediasplayer_Audioplayer.optionFloating .atuiMediasplayer_Close{
    display:block;
}

.atuiMediasplayer_Audioplayer .atuiMediasplayer_Cover{
    width:150px;
    height:150px;
    background-color:hsl(var(--atuiKernel_Color-N70));
    border-radius:var(--atuiKernel_Borderradius);
    overflow:hidden;
}

.atuiMediasplayer_Audioplayer .atuiMediasplayer_Cover img{
    height:100%;
}

.atuiMediasplayer_Audioplayer .atuiMediasplayer_Panel, .atuiMediasplayer_Audioplayer .atuiMediasplayer_Dashboard{
    display:flex;
}

.atuiMediasplayer_Audioplayer .atuiMediasplayer_Panel{
    gap:15px;
}

@media (min-width: 768px){
    .atuiMediasplayer_Audioplayer .atuiMediasplayer_Dashboard{
        margin:10px 0;
    }
}

@media (max-width: 767px){
    .atuiMediasplayer_Audioplayer .atuiMediasplayer_Panel{
        flex-direction:column;
        align-items:center;
    }

    .atuiMediasplayer_Audioplayer .atuiMediasplayer_Cover, .atuiMediasplayer_Audioplayer .atuiMediasplayer_Information{
        display:flex;
        justify-content:center;
        text-align:center;
    }
}

.atuiMediasplayer_Audioplayer .atuiMediasplayer_Dashboard{
    flex:1;
    row-gap:10px;
}

.atuiMediasplayer_Audioplayer .atuiMediasplayer_Information > p{
    font-size:1.125rem;
    font-weight:bold;
}

.atuiMediasplayer_Audioplayer .atuiMediasplayer_InformationTitle{
    color:hsl(var(--atuiKernel_Color-A40));
}

.atuiMediasplayer_Audioplayer .atuiMediasplayer_Close{
    display:none;
}

.atuiMediasplayer_Audioplayer .atuiMediasplayer_Progressbar{
    border-radius:0 0 var(--atuiKernel_Borderradius) var(--atuiKernel_Borderradius);
    overflow:hidden;
}

@media (min-width: 768px){
    .atuiMediasplayer_Videoplayer{
        display:flex;
        justify-content:center;
    }
}

.atuiMediasplayer_Videoplayer > article{
    overflow:hidden;
}

.atuiMediasplayer_Videoplayer video{
    max-width:100%;
    max-height:65vh;
    display:block;
}

.atuiMediasplayer_Videoplayer .atuiMediasplayer_Dashboard{
    display:flex;
    justify-content:center;
    width:100%;
}

.atuiMediasplayer_Videoplayer .atuiMediasplayer_Progressbar{
    width:100%;
}

.atuiMediasplayer_Videoplayer.optionOverlay .atuiMediasplayer_Dashboard{
    background-color:hsl(var(--atuiKernel_Color-N90) / 80%);
    opacity:0.2;
    position:absolute;
    bottom:0;
}

.atuiMediasplayer_Videoplayer.optionOverlay .atuiMediasplayer_Dashboard:hover{
    opacity:1;
}

.atuiMediasplayer_Audioplayer,
.atuiMediasplayer_Videoplayer > article{
    border:1px solid hsl(var(--atuiKernel_Color-N50));
    border-radius:var(--atuiKernel_Borderradius);
    position:relative;
}

.atuiMediasplayer_Audioplayer,
.atuiMediasplayer_Videoplayer .atuiMediasplayer_Dashboard{
    background-color:hsl(var(--atuiKernel_Color-N90));
}

.atuiMediasplayer_Audioplayer *, .atuiMediasplayer_Videoplayer *{
    margin:0;
}

.atuiMediasplayer_Audioplayer .atuiMediasplayer_Panel,
.atuiMediasplayer_Videoplayer .atuiMediasplayer_Controls{
    padding:10px;
}

@media (min-width: 768px){
    .atuiMediasplayer_Audioplayer .atuiMediasplayer_Panel,
    .atuiMediasplayer_Videoplayer .atuiMediasplayer_Controls{
        flex-direction:row;
    }
}

@media (max-width: 767px){
    .atuiMediasplayer_Controls{
        flex-direction:column;
    }
}

.atuiMediasplayer_Dashboard{
    flex-direction:column;
}

.atuiMediasplayer_Audioplayer .atuiMediasplayer_Information,
.atuiMediasplayer_Controls,
.atuiMediasplayer_Controls > div{
    display:flex;
    gap:10px;
    align-items:center;
    flex-wrap:wrap;
}

.atuiMediasplayer_Controls{
    justify-content:space-evenly;
}

.atuiMediasplayer_Controls i, .atuiMediasplayer_Controls img{
    width:30px;
    height:30px;
    padding:5px 10px;
    border-radius:var(--atuiKernel_Borderradius);
    font-size:30px;
}

.atuiMediasplayer_Controls i:hover, .atuiMediasplayer_Controls img:hover{
    background-color:hsl(var(--atuiKernel_Color-A70));
}

.atuiMediasplayer_Progressbar,
.atuiMediasplayer_ProgressbarInside{
    height:10px;
}

.atuiMediasplayer_ProgressbarInside{
    width:0;
    background-color:hsl(var(--atuiKernel_Color-A50));
    transition:width 500ms;
}

.atuiMediasplayer_Audioplayer .atuiKernel_SectionBox, .atuiMediasplayer_Videoplayer .atuiKernel_SectionBox{
    margin:10px;
}
